import React, {memo} from 'react';
import {Link} from "react-router-dom";


export default memo((props) => {
    return (
        <div style={{maxHeight : '80%'}}>

            <div id="main_not_found">

                <div className="container">
                    <div className="row">

                        <div className="col s12 l4 m6 hide-on-small-and-down" style={{padding : 40 , marginBottom : "-50px"}}>
                            <img src={window.location.origin + '/img/gallary/unnamed.png'}
                                 className="responsive-img" style={{width : 'auto' , height :'auto'}} />
                        </div>

                        <div className="col s12 l8 m6" style={{padding: 15 , marginTop : 30}}>

                            <p id="error_not_p">
                                <i className="material-icons">error_outline</i>404 error
                            </p>

                            <p id="desc_p_title">
                                <i className="material-icons">keyboard_arrow_right</i> Check the address url again
                            </p>

                            <p id="desc_p_title">
                                <i className="material-icons">keyboard_arrow_right</i> Comeback
                                to <a>home</a> or <Link to={"/signin"}><a>login</a></Link> page
                            </p>

                            <p id="desc_p_title">
                                <i className="material-icons">keyboard_arrow_right</i> Report an error to the
                                management and support team of the website
                            </p>
                            <p id="desc_p_title">
                                <i className="material-icons">keyboard_arrow_right</i> There may be other reasons
                                and you need to search the net.
                            </p>

                        </div>

                    </div>
                </div>

            </div>

        </div>
    )
})